@import './var.scss';

.container {
  color: $color-white;
  text-align: center;
  background-color: $color-primary-darker;
  border: 1px solid black;
  border-radius: 8px;
  max-width: 400px;
  margin: auto;
  padding: 24px;
}

.social {
  list-style: none;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 200px;
  margin: auto;
  padding: 0;
  margin-bottom: 10px;

  li {
    svg {
      height: 40px;
    }
  }


}

.button {
  background-color: $color-primary;
  border: solid 1px white;
  color: #f2f2f2;
  padding: 10px 20px;
  font-size: 16px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin: 24px !important;
  text-decoration: none;
}